import logo from './logo.svg';
import './App.css';
import { Navbar, Container, Nav, Button } from 'react-bootstrap';
import { useState } from 'react';
import shoes from './data.js';
import { Link, Route, Switch } from 'react-router-dom';
import DetailPage from './Detail.js';
import axios from 'axios';


function App() {

  let [products, setProducts] = useState(shoes);
  let [imgs, setImgs] = useState(['product1.jpg', 'product2.jpg', 'product3.jpg'])
  let [moreButton, setMoreButton] = useState(true);
  let [stock, setStock] = useState([10,11,12]);

  return (
    <div className="App">

      <Switch>
        <Route exact path="/">
          <MainPage />
        </Route>
        <Route path="/detail/:id">
          <NavBar />
          <DetailPage stock={stock} products={products} />
        </Route>
        <Route path="/:id">
          <NavBar />
        </Route>

      </Switch>







    </div>
  );


  function NavBar() {
    return (
      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand><Link className='link' to={"/"}>rUDIE's</Link></Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link><Link className='link' to={"/"}>Home</Link></Nav.Link>
            <Nav.Link><Link className='link' to={"/detail"}>Detail</Link></Nav.Link>
            <Nav.Link href="#pricing">Pricing</Nav.Link>
          </Nav>
        </Container>
      </Navbar>
    )
  }
  function Shoes(props) {
    return (

      <div className='col-md-4'>
        <img src={ 'https://codingapple1.github.io/shop/shoes' + (props.i+1) + '.jpg'} width='100%'></img>
        <h4>{props.title}</h4>
        <p>{props.price} {props.content}</p>
      </div>

    );
  }


  function MainPage() {
    return (
      <div>
        <NavBar />
        <div className='background'>
          <h1>End of year sale, up to 50%</h1>
          <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book.  </p>
          <Button variant="primary">Learn more</Button>{' '}
        </div>

        <br />

        <div className='container'>
          <div className='row'>
            {products.map(function (shoe, i) {
              return (
                <Shoes shoes={products[i]} i={i} key={i} />
              );
            })}
          </div>
          {
            moreButton === true ? <button className='btn btn-primary' onClick={() => {
               setMoreButton(false);
              axios.get('https://codingapple1.github.io/shop/data2.json')
              .then((result) => {
                console.log(result.data);
                setProducts([...shoes,...result.data]);
              })
              .catch(() => { console.log('fail') })
              ;
            }}>more</button>
            :null
            
          }
         

          
        </div>
      </div>

    );
  }


}

export default App;
