import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from "react-router-dom";
import styled from 'styled-components';
import './detail.scss';

export default
    function DetailPage(props) {
    let { id } = useParams();
    let history = useHistory();
    let selectedProduct = props.products.find((shoe) => { return shoe.id == id });
    let [alert,setAlert] = useState(true);
    let [inputData,setInputData] = useState('');
    
        useEffect(()=>{let timer = setTimeout(() => {
            setAlert(false)}, 2000);
            return ()=>{clearTimeout(timer)}
    },[]);
        
        


    return (
        <div>
           <h4 className='red'>this is red</h4>
            
           {alert === true ? (<div className='alert'>
               <p>Only 2 left</p>
           </div>)
           :null
           }

           <input onChange={(e)=>{setInputData(e.target.value)}}/>

            <div className='container'>
                <div className='row'>
                    <div className='col-md-6'>
                        <img src={"https://codingapple1.github.io/shop/shoes1.jpg"} width={'100%'}></img>
                    </div>
                    <div className='col-md-6 mt-4'>

                        <h4 className='pt-5'>{selectedProduct.title}</h4>
                        <p>{selectedProduct.content}</p>
                        <p>{selectedProduct.price}</p>
                        <Info stock ={props.stock}/>
                        <button className='btn btn-danger'>Order</button>
                        <button className="btn btn-danger" onClick={() => { history.goBack() }}>Go back</button>
                    </div>
                </div>
            </div>
        </div>
    );

    function Info(props){
        return(
            <p>{props.stock[0]}</p>
        );
    }
}